import { Image as TiptapImage } from "tiptap-extensions";
import { TextSelection, Plugin } from "tiptap";

import ImageComponent from "./Image.vue";

export default class ImageNode extends TiptapImage {
  get schema() {
    return {
      attrs: {
        src: {
          default: ""
        },
        alt: {
          default: ""
        },
        caption: {
          default: ""
        }
      },
      group: "block",
      selectable: false,
      parseDOM: [
        {
          tag: 'img[src][data-featured-image="false"]',
          getAttrs: dom => {
            return {
              src: JSON.parse(dom.getAttribute("data-src")),
              alt: dom.getAttribute("alt"),
              caption: dom.getAttribute("caption")
            };
          }
        }
      ],
      toDOM: node => {
        if (node.attrs.src === "") return ["paragraph"];
        return [
          "img",
          {
            ...node.attrs,
            src: node.attrs.src && node.attrs.src.fallback,
            "data-src": JSON.stringify(node.attrs.src),
            "data-featured-image": false
          }
        ];
      }
    };
  }

  commands({ type }) {
    return ({ src, addImageAt }) => (state, dispatch) => {
      let { tr, schema } = state;
      if (tr.doc.content.size - addImageAt === 1)
        tr = tr.insert(tr.doc.content.size, schema.nodes["paragraph"].create());
      let textSelection = TextSelection.create(tr.doc, addImageAt, addImageAt);
      tr = tr
        .setSelection(textSelection)
        .replaceSelectionWith(type.create({ src }));
      return dispatch(tr);
    };
  }

  get plugins() {
    return [
      new Plugin({
        props: {
          handleDOMEvents: {
            drop: (view, event) => {
              const hasFiles =
                event.dataTransfer &&
                event.dataTransfer.files &&
                event.dataTransfer.files.length;

              if (!hasFiles) {
                return;
              }

              const images = Array.from(event.dataTransfer.files).filter(file =>
                /image/i.test(file.type)
              );

              if (images.length === 0) {
                return;
              }

              event.preventDefault();

              const { schema } = view.state;
              const coordinates = view.posAtCoords({
                left: event.clientX,
                top: event.clientY
              });
              const fImgPos = view.fImg.getPos();

              images.forEach(image => {
                const reader = new FileReader();

                reader.onload = readerEvent => {
                  this.commands({ type: schema.nodes.image })({
                    src: { fallback: readerEvent.target.result, file: image },
                    addImageAt:
                      coordinates.pos <= fImgPos + 1
                        ? fImgPos + 1
                        : coordinates.pos
                  })(view.state, view.dispatch);
                };
                reader.readAsDataURL(image);
              });
            }
          }
        }
      })
    ];
  }

  get view() {
    return {
      props: ["node", "updateAttrs", "view", "getPos", "options"],
      components: {
        ImageComponent
      },
      template: `
        <ImageComponent
          :node='this.node'
          :updateAttrs='this.updateAttrs'
          :view='this.view'
          :getPos='this.getPos'
          :options='this.options'
        ></ImageComponent>
      `
    };
  }
}
